@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  font-family: 'Indie Flower', cursive; }

body {
  margin: 0; }

#root {
  width: 100vw;
  height: 100vh;
  background: url(https://i.postimg.cc/L6p72h7N/Chalkboard-Background-1.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden; }

.app-bar {
  display: flex;
  justify-content: flex-end;
  padding: 1%;
  top: 0;
  width: 100%; }
  .app-bar .icon {
    width: 30px;
    height: 30px;
    cursor: pointer; }
    .app-bar .icon:hover {
      -webkit-animation: wobble 1s;
              animation: wobble 1s; }

.title-container {
  color: #f1f1f1;
  text-align: center;
  margin-top: 5%;
  margin-bottom: 2%;
  font-size: 1.5em; }

.grid {
  display: grid;
  grid-template-columns: repeat(3, 150px); }
  @media only screen and (max-width: 450px) {
    .grid {
      grid-template-columns: repeat(3, calc(150px - 50px)); } }
  .grid .cell {
    border: #f1f1f1 3px solid;
    background-color: transparent;
    flex: 0 0 32%;
    height: 150px;
    display: flex;
    justify-content: center;
    outline: none;
    position: relative;
    cursor: pointer; }
    @media only screen and (max-width: 450px) {
      .grid .cell {
        height: calc(150px - 50px); } }
    .grid .cell.cell-0-0 {
      border-left: none;
      border-top: none; }
    .grid .cell.cell-0-1 {
      border-top: none; }
    .grid .cell.cell-0-2 {
      border-top: none;
      border-right: none; }
    .grid .cell.cell-1-0 {
      border-left: none; }
    .grid .cell.cell-1-2 {
      border-right: none; }
    .grid .cell.cell-2-0 {
      border-left: none;
      border-bottom: none; }
    .grid .cell.cell-2-1 {
      border-bottom: none; }
    .grid .cell.cell-2-2 {
      border-right: none;
      border-bottom: none; }
    .grid .cell .hoverP1 {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 30px; }
      .grid .cell .hoverP1:hover {
        background: rgba(241, 241, 241, 0.1); }
      .grid .cell .hoverP1:before {
        content: '';
        width: 15px;
        height: 120px;
        background: rgba(45, 45, 45, 0.8);
        position: absolute;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg); }
      .grid .cell .hoverP1::after {
        content: '';
        width: 15px;
        height: 120px;
        background: rgba(45, 45, 45, 0.8);
        position: absolute;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg); }
    .grid .cell .player1 {
      background: #f1f1f1;
      height: 120px;
      width: 15px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      -webkit-transition: -webkit-transform 0.5s;
      transition: -webkit-transform 0.5s;
      transition: transform 0.5s;
      transition: transform 0.5s, -webkit-transform 0.5s; }
      .grid .cell .player1::after {
        background: #f1f1f1;
        content: '';
        height: 15px;
        left: -50px;
        position: absolute;
        top: 50px;
        width: 120px; }
    .grid .cell .player2 {
      border: 15px solid #f1f1f1;
      border-radius: 50%;
      height: 80px;
      width: 80px;
      -webkit-transition: all 0.5s;
      transition: all 0.5s; }

.backdrop {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 9998;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center; }
  .backdrop .modal-container {
    width: 250px;
    height: 125px;
    position: relative;
    padding: 1% 2% 2% 2%;
    z-index: 9999;
    border-radius: 0.5rem;
    background-color: #f1f1f1;
    display: flex;
    justify-content: center; }
    .backdrop .modal-container .message-container {
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      font-size: 20px;
      font-weight: bold; }
      .backdrop .modal-container .message-container button {
        font-size: 20px;
        font-weight: bold;
        background-color: #48c9b0;
        border: none;
        color: #f1f1f1;
        border-radius: 5px;
        width: 75px;
        height: 30px;
        cursor: pointer; }
        .backdrop .modal-container .message-container button:hover {
          background-color: #278472; }

@-webkit-keyframes wobble {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  25% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg); }
  50% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg); }
  75% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg); }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

@keyframes wobble {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  25% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg); }
  50% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg); }
  75% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg); }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

